<template>
  <div id="app">
    <router-view />
  </div>
</template>
<script>
export default {
  name: "app",
  data() {
    return {
      theme: "darkMode", //When this property is empty, the theme is set to the default theme i.e. light mode.
    };
  },
  mounted() {
    document.title = "ZeroDrop-A Data-Driven Web3 Marketing Platform.";
    console.log(window.matchMedia("(prefers-color-scheme: dark)").matches);
    this.detectColorScheme();
    // document.documentElement.setAttribute("data-theme", "dark");
  },
  methods: {
    toggleTheme() {
      this.theme = this.theme == "darkMode" ? "" : "darkMode"; //toggles theme value
      document.documentElement.setAttribute("data-theme", this.theme); // updates the data-theme attribute
      localStorage.setItem("theme", this.theme); // stores theme value in local storage
    },
    detectColorScheme() {
      var theme = "dark"; //default to dark

      //local storage is used to override OS theme settings
      if (localStorage.getItem("theme")) {
        if (localStorage.getItem("theme") == "light") {
          var theme = "dark";
        }
      } else if (!window.matchMedia) {
        //matchMedia method not supported
        return false;
      } else if (window.matchMedia("(prefers-color-scheme: dark)").matches) {
        //OS theme setting detected as dark
        var theme = "dark";
      }

      //dark theme preferred, set document with a `data-theme` attribute
      if (theme == "dark") {
        document.documentElement.setAttribute("data-theme", "dark");
      }
    },
  },
};
</script>

